window.jQuery=function(selectorOrArray){
    let elements
    if(typeof selectorOrArray === 'string'){
        elements=document.querySelectorAll(selectorOrArray)
    }else if(selectorOrArray instanceof Array){
        elements=selectorOrArray
    }
 
 return{
     addClass(className){
         for(let i=0;i<elements.length;i++){
             const element=elements[i]
             element.classList.add(className)
         }
         return this
     },
     find(selector){
       let array=[]
       for(let i=0;i<elements.length;i++){//ç”¨elementä»£æ›¿documentæŸ¥æ‰¾èŒƒå›´
        const elements2=Array.from(elements[i].querySelectorAll(selector))
        array=array.concat(elements2)
}
     array.oldApi=this//thisæ˜¯oldApi
     return jQuery(array)
     },
     each(fn){//éåŽ†å½“å‰æ‰€æœ‰å…ƒç´ ï¼Œä½ ç»™æˆ‘ä¸ªå‡½æ•°å°±è¡Œäº†
         for(let i=0;i<elements.length;i++){
             fn.call(null,elements[i],i)
         }
         return this//thiså°±æ˜¯å½“å‰çš„api
     },
     parent(){
        const array = []
        this.each((node)=>{
          if(array.indexOf(node.parentNode) === -1){
            array.push(node.parentNode)
          }
        })
        return jQuery(array)
      },
      children(){
        const array = []
        this.each((node)=>{
          array.push(...node.children)
        })
        return jQuery(array)
      },
      print(){
        console.log(elements)
      },
     oldApi:selectorOrArray.oldApi,
     end(){
       return this.oldApi //thisæ˜¯newApi
}
 }
}